<template>
  <div>
    <!-- v-tooltip -->

    <div class="d-flex">
      <v-col cols="3"
             style="background-color: #eeeef0;">
        <v-tabs v-model="tab"
                :vertical=true
                background-color="#ffffff"
                color='primary'
                dark>
          <v-tab v-for="item in items"
                 style="color:black"
                 :key="item.tab">
            <v-tooltip bottom>
              <template v-slot:activator="{ on, attrs }">
                <span v-bind="attrs"
                      v-on="on"
                      style="overflow: hidden;text-overflow:ellipsis;white-space:nowrap;max-width:200px;min-width:100px;text-align:left">{{item.tab}}</span>
              </template>
              <span> {{ item.tab }}</span>
            </v-tooltip>
          </v-tab>
        </v-tabs>
      </v-col>
      <v-col cols="9"
             class="pl-0 pr-0 pt-0 pb-0">
        <v-tabs-items v-model="tab">
          <!-- 员工信息 -->
          <v-tab-item>
            <employeemsg :disabled='iconstatus'></employeemsg>
          </v-tab-item>
          <!-- 员工个人信息 -->
          <v-tab-item>
            <employeeonemsg :disabled='iconstatus'></employeeonemsg>
          </v-tab-item>
          <!-- 就业信息 -->
          <v-tab-item>
            <v-card>
              <v-card-text>
                <v-row>
                  <v-col cols="6">
                    <mrc_ess_datePicker @getTime='paramsChange'
                                        :disabled="iconstatus"
                                        :label="$t('label_The_date_of_first_joining_the_company')+'*'"
                                        :errMsg="$errors('jobslist','first_employment_date',[{rule:'jobsbeforeDate',msg:$t('verify_The_date_was_filled_in_incorrectly')}])"
                                        v-model="jobslist.first_employment_date"></mrc_ess_datePicker>
                  </v-col>
                  <v-col cols="6">
                    <mrc_ess_datePicker @getTime='paramsChange'
                                        :disabled="iconstatus"
                                        :errMsg="$errors('jobslist','employment_date',[{rule:'jobsCenterDate',msg:$t('verify_The_date_was_filled_in_incorrectly')}])"
                                        :label="$t('label_Thedate_of_recent_joining_the_company')+'*'"
                                        v-model="jobslist.employment_date"></mrc_ess_datePicker>
                  </v-col>
                  <v-col cols="6">
                    <mrc_ess_datePicker @getTime='paramsChange'
                                        :disabled="iconstatus"
                                        :label="$t('label_The_end_date_of_the_trial_period')+'*'"
                                        :errMsg="$errors('jobslist','probation_end_date',[{rule:'jobsafterDate',msg:$t('verify_The_date_was_filled_in_incorrectly')}])"
                                        v-model="jobslist.probation_end_date"></mrc_ess_datePicker>
                  </v-col>
                  <v-col cols="6">
                    <mrc_ess_datePicker @getTime='paramsChange'
                                        :label="$t('label_The_date_of_separation')+'*'"
                                        v-model="jobslist.termination_notification_date"></mrc_ess_datePicker>
                  </v-col>
                </v-row>
              </v-card-text>
              <mrc_ess_submitBtn @success='datesuccess'
                                 @cancel='datecancel'
                                 :loading='btnIsLoading'
                                 v-if='!iconstatus'></mrc_ess_submitBtn>
            </v-card>

          </v-tab-item>
          <!-- 银行信息 -->
          <v-tab-item>
            <v-card>
              <v-card-text>
                <v-data-table :headers="headers"
                              :items="blanklist"
                              sort-by="calories"
                              class="elevation-1"
                              hide-default-footer
                              @click:row='godetail'>

                  <template v-slot:item.is_used="{item}">
                    <v-btn rounded
                           color="green"
                           dark
                           x-small
                           v-if="item.is_used==1">{{$t('label_To_enable_the')}}</v-btn>
                    <v-btn rounded
                           color="red"
                           dark
                           x-small
                           v-else>{{$t('label_disable')}}</v-btn>
                  </template>

                  <template v-slot:item.actions="{ item }"
                            v-if="!iconstatus">
                    <v-icon @click="editItem(item)">mdi-pencil</v-icon>
                    <v-icon @click="deleteItem(item)"
                            class="m1">mdi-delete</v-icon>
                  </template>

                  <template v-slot:footer>
                    <v-divider></v-divider>
                    <span v-if="!iconstatus"
                          class="mt-2 pb-2 d-flex ml-5"
                          @click="editItem">
                      <span :style="{'color': $vuetify.theme.defaults.light.primary,'line-height': '28px','font-size':'14px'}">{{$t('label_New_bank_account')}}</span>
                      <v-icon class="material-icons"
                              :style="{'color':$vuetify.theme.defaults.light.primary,'font-size':'24px'}">add</v-icon>
                    </span>
                  </template>
                </v-data-table>
              </v-card-text>
              <mrc_ess_submitBtn @success='blanksuccess'
                                 @cancel='blankcancel'
                                 :loading='btnIsLoading'
                                 v-if='!iconstatus'>
              </mrc_ess_submitBtn>
            </v-card>

          </v-tab-item>
          <!-- 紧急联系人 -->
          <v-tab-item>
            <v-card>
              <v-card-text>
                <div v-if="!iconstatus"
                     style="height:40px">
                  <v-btn rounded
                         color="#ffffff"
                         dark
                         style="position: absolute;right: 10px;color: black;"
                         @click="editItemtel"><span style="color: #0F46C1;font-size: 30px;">+</span>{{$t('label_New_emergency_contact')}}</v-btn>
                </div>
                <v-card v-for="(item,index) in tellist"
                        :key="index"
                        class="m2">
                  <v-card-title class="telheader">
                    <span class="c1">{{$t('label_Emergency_contact')}}{{index+1}}</span>
                    <div v-if="!iconstatus">
                      <v-icon class="mr-2"
                              @click="editItemtel(item)">
                        mdi-pencil
                      </v-icon>
                      <v-icon @click="deleteItemtel(item)">
                        mdi-delete
                      </v-icon>
                    </div>
                  </v-card-title>
                  <v-card-text>
                    <v-row>
                      <v-col cols="6">
                        <!-- <v-text-field label="关系"
                                  :readonly="iconstatus"
                                  v-model="item.emergency_contact_relationship">
                    </v-text-field> -->
                        <mrc_ess_multiText :label="$t('label_Relationship')"
                                           :readonly="iconstatus"
                                           :isNational="judegNational('emergency_contact_relationship')"
                                           v-model="item.emergency_contact_relationship">
                        </mrc_ess_multiText>
                      </v-col>
                      <v-col cols="6">
                        <!-- <v-text-field label="全名字"
                                  :readonly="iconstatus"
                                  v-model="item.emergency_contact_given_name">
                    </v-text-field> -->
                        <mrc_ess_multiText :label="$t('label_Full_name')"
                                           :readonly="iconstatus"
                                           :isNational="judegNational('emergency_contact_given_name')"
                                           v-model="item.emergency_contact_given_name">
                        </mrc_ess_multiText>
                      </v-col>
                      <v-col cols="6">
                        <v-text-field :label="$t('label_Contact_number')"
                                      :readonly="iconstatus"
                                      v-model="item.phone_number">
                        </v-text-field>
                      </v-col>
                      <v-col cols="6">
                        <!-- <v-text-field label="邮箱"
                                  :readonly="iconstatus"
                                  v-model="item.emergency_contact_email">
                    </v-text-field> -->
                        <mrc_ess_multiText :label="$t('label_Mailbox')"
                                           :isNational="judegNational('emergency_contact_email')"
                                           :readonly="iconstatus"
                                           v-model="item.emergency_contact_email">
                        </mrc_ess_multiText>
                      </v-col>
                      <v-col cols="12">
                        <v-text-field class="m3"
                                      :label="$t('label_The_mailing_address')"
                                      :readonly="true"
                                      height="25"
                                      v-model="item.emergency_contact_address_line_1">
                        </v-text-field>
                      </v-col>
                      <v-col cols="12">
                        <v-text-field class="m3"
                                      :readonly="true"
                                      height="25"
                                      v-model="item.emergency_contact_address_line_2">
                        </v-text-field>
                      </v-col>
                      <v-col cols="12">
                        <v-text-field class="m3"
                                      :readonly="true"
                                      height="25"
                                      v-model="item.emergency_contact_address_line_3">
                        </v-text-field>
                      </v-col>
                      <!-- <v-col cols="12">
                    <mrc_ess_multiText class="m3"
                                       label="通讯地址"
                                       :isNational="judegNational('emergency_contact_address_line_1')"
                                       :readonly="iconstatus"
                                       height="25"
                                       v-model="item.emergency_contact_address_line_1">
                    </mrc_ess_multiText>
                  </v-col>
                  <v-col cols="12">
                    <mrc_ess_multiText class="m3"
                                       :isNational="judegNational('emergency_contact_address_line_2')"
                                       :readonly="iconstatus"
                                       height="25"
                                       v-model="item.emergency_contact_address_line_2">
                    </mrc_ess_multiText>
                  </v-col>
                  <v-col cols="12">
                    <mrc_ess_multiText class="m3"
                                       :isNational="judegNational('emergency_contact_address_line_3')"
                                       :readonly="iconstatus"
                                       height="25"
                                       v-model="item.emergency_contact_address_line_3">
                    </mrc_ess_multiText>
                      </v-col> -->
                    </v-row>
                  </v-card-text>
                </v-card>
              </v-card-text>
              <mrc_ess_submitBtn @success='telsuccess'
                                 @cancel='telcancel'
                                 :loading='btnIsLoading'
                                 v-if='!iconstatus'>
              </mrc_ess_submitBtn>
            </v-card>
          </v-tab-item>

        </v-tabs-items>

      </v-col>
    </div>

    <!-- 紧急联系人弹出框 -->
    <mrc_ess_myDialog v-model="dialog2"
                      :title="$t('label_New_emergency_contact')"
                      :width='800'
                      @success='telsave'
                      @cancel='telclose'>
      <v-card-text>
        <v-row>
          <v-col cols="12">
            <mrc_ess_multiText :label="$t('label_Relationship')+'*'"
                               :isNational="judegNational('emergency_contact_relationship')"
                               :readonly="iconstatus"
                               :error-messages="$errors('editedtelItem','emergency_contact_relationship',requiredRule.concat(nocharacter))"
                               v-model="editedtelItem.emergency_contact_relationship">
            </mrc_ess_multiText>
          </v-col>
          <v-col cols="6">
            <mrc_ess_multiText :label="$t('label_The_name')+'*'"
                               :isNational="judegNational('emergency_contact_given_name')"
                               :readonly="iconstatus"
                               :error-messages="$errors('editedtelItem','emergency_contact_given_name',requiredRule.concat(nocharacter))"
                               v-model="editedtelItem.emergency_contact_given_name">
            </mrc_ess_multiText>
          </v-col>
          <v-col cols="6">
            <mrc_ess_multiText :label="$t('label_Surname')+'*'"
                               :isNational="judegNational('emergency_contact_family_name')"
                               :readonly="iconstatus"
                               :error-messages="$errors('editedtelItem','emergency_contact_family_name',requiredRule.concat(nocharacter))"
                               v-model="editedtelItem.emergency_contact_family_name">
            </mrc_ess_multiText>
          </v-col>
          <v-col cols="6">
            <mrc_ess_multiText :label="$t('label_Other_names')+'*'"
                               :isNational="judegNational('emergency_contact_christian_name')"
                               :readonly="iconstatus"
                               :error-messages="$errors('editedtelItem','emergency_contact_christian_name',requiredRule.concat(nocharacter))"
                               v-model="editedtelItem.emergency_contact_christian_name">
            </mrc_ess_multiText>
          </v-col>
          <v-col cols="6"></v-col>
          <v-col cols="2">
            <mrc_ess_prompt :label="$t('label_The_area_code')+'*'"
                            :disabled='iconstatus'
                            :rules='false'
                            :chips="false"
                            :errMsg="$errors('editedtelItem','emergency_contact_area_code',requiredRule)"
                            v-model="editedtelItem.emergency_contact_area_code"
                            obj="country_mobile_prefix">
            </mrc_ess_prompt>
          </v-col>

          <v-col cols="4">
            <!-- <v-text-field label="联系电话*"
                          :readonly="iconstatus"
                          :error-messages="$errors('editedtelItem','emergency_contact_phone_number',[{rule:'areaRequired',msg:'区号没填'},{rule:'phoneVerify',msg:'电话号码格式不对,请输入正确的号码格式'}].concat(requiredRule))"
                          v-model="editedtelItem.emergency_contact_phone_number">
            </v-text-field> -->
            <v-text-field :label="$t('label_Contact_number')+'*'"
                          :readonly="iconstatus"
                          v-model="editedtelItem.emergency_contact_phone_number">
            </v-text-field>
          </v-col>
          <!-- <v-col cols="6">
            <v-text-field label="邮箱"
                          :error-messages="$errors('editedtelItem','emergency_contact_email',emailRule)"
                          :readonly="iconstatus"
                          v-model="editedtelItem.emergency_contact_email">
            </v-text-field>
          </v-col>
          <v-col cols="12">
            <v-text-field class="m3"
                          label="通讯地址"
                          :readonly="iconstatus"
                          height="25"
                          v-model="editedtelItem.emergency_contact_address_line_1">
            </v-text-field>
          </v-col>
          <v-col cols="12">
            <v-text-field class="m3"
                          :readonly="iconstatus"
                          height="25"
                          v-model="editedtelItem.emergency_contact_address_line_2">
            </v-text-field>
          </v-col>
          <v-col cols="12">
            <v-text-field class="m3"
                          :readonly="iconstatus"
                          height="25"
                          v-model="editedtelItem.emergency_contact_address_line_3">
            </v-text-field>
          </v-col> -->
          <v-col cols="6">
            <mrc_ess_multiText :label="$t('label_Mailbox')"
                               :isNational="judegNational('emergency_contact_email')"
                               :error-messages="$errors('editedtelItem','emergency_contact_email',emailRule)"
                               :readonly="iconstatus"
                               v-model="editedtelItem.emergency_contact_email">
            </mrc_ess_multiText>
          </v-col>
          <v-col cols="12">
            <mrc_ess_multiText class="m3"
                               :label="$t('label_The_mailing_address')"
                               :isNational="judegNational('emergency_contact_address_line_1')"
                               :readonly="iconstatus"
                               height="25"
                               v-model="editedtelItem.emergency_contact_address_line_1">
            </mrc_ess_multiText>
          </v-col>
          <v-col cols="12">
            <mrc_ess_multiText class="m3"
                               :isNational="judegNational('emergency_contact_address_line_2')"
                               :readonly="iconstatus"
                               height="25"
                               v-model="editedtelItem.emergency_contact_address_line_2">
            </mrc_ess_multiText>
          </v-col>
          <v-col cols="12">
            <mrc_ess_multiText class="m3"
                               :isNational="judegNational('emergency_contact_address_line_3')"
                               :readonly="iconstatus"
                               height="25"
                               v-model="editedtelItem.emergency_contact_address_line_3">
            </mrc_ess_multiText>
          </v-col>
        </v-row>
      </v-card-text>
    </mrc_ess_myDialog>

    <!-- 银行弹出框 -->
    <mrc_ess_myDialog v-model="dialog"
                      :title="$t('label_New_bank_account')"
                      @success='save'
                      @cancel='close'>
      <v-card-text>
        <v-row dense>
          <v-col cols="12">
            <mrc_ess_multiText v-model="editedItem.bank_name"
                               :isNational="judegNational('bank_name')"
                               @input="$v.editedItem.bank_name.$touch()"
                               :error-messages="$errors('editedItem','bank_name',requiredRule.concat(nocharacter))"
                               :label="$t('label_The_name_of_the_bank')+'*'"></mrc_ess_multiText>
          </v-col>
          <v-col cols="12">
            <mrc_ess_multiText v-model="editedItem.bank_account_number"
                               :isNational="judegNational('bank_account_number')"
                               @input="$v.editedItem.bank_account_number.$touch()"
                               :error-messages="$errors('editedItem','bank_account_number',requiredRule.concat(numericRule))"
                               :label="$t('label_Account_number')+'*'"></mrc_ess_multiText>
          </v-col>
          <v-col cols="12">
            <mrc_ess_multiText v-model="editedItem.bank_account_holder_name"
                               :isNational="judegNational('bank_account_holder_name')"
                               :error-messages="$errors('editedItem','bank_account_holder_name',requiredRule.concat(nocharacter))"
                               :label="$t('label_Register_name')"></mrc_ess_multiText>
          </v-col>
          <v-col cols="5">
            <mrc_ess_datePicker @getTime='paramsChange'
                                :disabled='false'
                                :errMsg="$errors('editedItem','effective_start_date',requiredRule.concat([{rule:'beforeDate',msg:'不能超过终止日期'}]))"
                                :label="$t('label_Effective_date')+'*'"
                                v-model="editedItem.effective_start_date"></mrc_ess_datePicker>
          </v-col>
          <v-col cols="2"
                 class="f2"> ></v-col>
          <v-col cols="5">
            <mrc_ess_datePicker @getTime='paramsChange'
                                :disabled='false'
                                :errMsg="$errors('editedItem','effective_end_date',requiredRule.concat([{rule:'afterDate',msg:'不能晚于开始时间'}]))"
                                v-model="editedItem.effective_end_date"></mrc_ess_datePicker>
          </v-col>
        </v-row>
      </v-card-text>
    </mrc_ess_myDialog>
  </div>
</template> 

<script>
import employeemsg from './employeemsg'
import employeeonemsg from './employeeone_msg'
import { required, maxLength, between, email, url, numeric } from 'vuelidate/lib/validators'
import { getStaffTelUpdate, getStaffTelList, getStaffBankUpdate, getStaffBank, getStaffMsgDetail, getStaffJobDetail, getStaffJobUpdate } from '@/service/api/staffmsg.js'
import moment from 'moment'
export default {
  components: {
    employeemsg,
    employeeonemsg,
  },
  props: {
    iconstatus: {
      type: Boolean
    }
  },
  data () {
    return {
      btnIsLoading: false,
      dialog2: false,
      dialog: false,
      disabled: false,
      stafflist: '',
      tab: null,
      jobslist: {
        staff_uuid: "",    //关联staff表uuid
        first_employment_date: null,    //第一次加入公司时间
        employment_date: null,    //最近加入公司时间
        probation_end_date: null,    //试用结束日期
        termination_notification_date: null,    //离职通知日期
        termination_effective_date: null,    //离职生效日期(只读)
      },
      blanklist: [
        {
          staff_bank_account_id: 11,    //物理主键
          uuid: "",    //唯一标识
          staff_uuid: "",    //关联staff表uuid
          bank_name: "",    //开户行名称
          bank_account_number: "",    //银行卡号码
          bank_account_holder_name: "",    //登记名称
        }
      ],
      deleteblanklist: [],//删除银行列表
      editedIndex: -1,
      editedItem: {
        bank_name: "",    //开户行名称
        bank_account_number: "",    //银行卡号码
        bank_account_holder_name: "",    //登记名称
        effective_start_date: '',
        effective_end_date: ''
      },
      defaultItem: {
        bank_name: "",    //开户行名称
        bank_account_number: "",    //银行卡号码
        bank_account_holder_name: "",    //登记名称
        effective_start_date: '',
        effective_end_date: ''
      },
      tellist: [
        // {
        //   staff_emergency_contact_info_id: '',
        //   uuid: '',
        //   staff_uuid: '',
        //   emergency_contact_relationship: '',
        //   emergency_contact_family_name: '',
        //   emergency_contact_given_name: '',
        //   emergency_contact_christian_name: '',
        //   emergency_contact_area_code: '',
        //   emergency_contact_phone_number: '',
        //   emergency_contact_email: '',
        //   emergency_contact_address_line_1: '',
        //   emergency_contact_address_line_2: '',
        //   emergency_contact_address_line_3: '',
        //   phone_number: '' //自己拼接的参数 视图显示
        // },
      ],       //紧急联系人列表
      deletetellist: [], //删除紧急联系人列表
      editedtelIndex: -1,
      editedtelItem: {
        emergency_contact_relationship: '',
        emergency_contact_given_name: '',
        emergency_contact_family_name: '',
        emergency_contact_christian_name: '',
        emergency_contact_area_code: '',
        emergency_contact_phone_number: '',
        emergency_contact_email: '',
        emergency_contact_address_line_1: '',
        emergency_contact_address_line_2: '',
        emergency_contact_address_line_3: '',
      },
      defaulteltItem: {
        emergency_contact_relationship: '',
        emergency_contact_given_name: '',
        emergency_contact_family_name: '',
        emergency_contact_christian_name: '',
        emergency_contact_area_code: '',
        emergency_contact_phone_number: '',
        emergency_contact_email: '',
        emergency_contact_address_line_1: '',
        emergency_contact_address_line_2: '',
        emergency_contact_address_line_3: '',
      },
    };
  },
  created () { },
  async mounted () {
    await this.national('mrbase_sys_staff_employment_info')
    // await this.national('mrbase_sys_staff_bank_account')
    // await this.national('mrbase_sys_staff_emergency_contact_info') 
    await this.getjoblist()
    await this.getblanklist()
    await this.gettellist()
  },
  methods: {
    async getjoblist () {
      const uuid = this.$store.state.uuid
      const res = await getStaffJobDetail({
        staff_uuid: uuid
      })
      if (res.code === '200') {
        this.jobslist = res.data
      }
    },
    async getblanklist () {
      const uuid = this.$store.state.uuid
      const res = await getStaffBank({
        staff_uuid: uuid
      })
      if (res.code === '200') {
        this.blanklist = res.data
      }
    },
    async gettellist () {
      const uuid = this.$store.state.uuid
      const res = await getStaffTelList({
        staff_uuid: uuid
      })
      if (res.code === '200') {
        // console.log(res.data);
        // this.$deletefield(res.data) 
        this.tellist = this.$deletefield(res.data).map(item => {
          item['phone_number'] = '+' + item.emergency_contact_area_code + '\xa0' + item.emergency_contact_phone_number
          return item
        })
        // console.log(this.tellist);
      }
    },
    paramsChange () {

    },
    datesuccess () { //员工就业信息提交数据
      //  moment(time).format('YYYY-MM-DD')
      this.$v.jobslist.$touch()
      if (this.$v.jobslist.$error) return
      this.btnIsLoading = true
      this.jobslist.staff_uuid = this.$store.state.uuid
      for (const key in this.jobslist) {
        if (key.includes('date')) {
          if (this.jobslist[key]) {
            this.jobslist[key] = moment(this.jobslist[key]).format('YYYY-MM-DD')
          } else {
            delete this.jobslist[key]
          }
        }
      }
      // console.log(this.jobslist);
      getStaffJobUpdate(this.jobslist).then(async res => {
        if (res.code === '200') {
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
          await this.getjoblist()
          this.btnIsLoading = false
        } else {
          this.$store.commit('newsTips', this.$t('message_The_modification_failed'))
          this.btnIsLoading = false
        }
      })
    },
    blanksuccess () { //银行信息提交
      this.btnIsLoading = true
      const uuid = this.$store.state.uuid
      const detailuuid = this.$store.state.detailuuid
      this.blanklist.map(item => {
        delete item.created_by
        delete item.created_utc_datetime
        delete item.updated_by
        delete item.updated_utc_datetime
        if (!item.status) item.status = 1
        if (item.effective_start_date || item.effective_end_date) {
          item.effective_start_date = moment(item.effective_start_date).format('YYYY-MM-DD')
          item.effective_end_date = moment(item.effective_end_date).format('YYYY-MM-DD')
        } else {
          delete item.effective_start_date
          delete item.effective_end_date
        }
      })
      getStaffBankUpdate({
        uuid, user_id: detailuuid, mrbase_sys_staff_bank_account: [...this.blanklist, ...this.deleteblanklist]
      }).then(async res => {
        if (res.code === '200') {
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
          await this.getblanklist()
          this.btnIsLoading = false
        } else {
          this.$store.commit('newsTips', this.$t('message_The_modification_failed'))
          this.btnIsLoading = false
        }
      })
    },
    blankcancel () {
      this.$myConfirm({
        content: '确认取消编辑'
      }).then(res => {
        this.$router.push({
          name: 'staffmanagement'
        })
      }).catch(err => { })
    },
    telsuccess () { //紧急联系人提交 
      this.btnIsLoading = true
      const uuid = this.$store.state.uuid
      const user_id = this.$store.state.detailuuid
      const mrbase_sys_staff_emergency_contact_info = this.tellist.map(item => {
        delete item.phone_number
        return item
      })
      getStaffTelUpdate({ uuid, user_id, mrbase_sys_staff_emergency_contact_info: [...this.deletetellist, ...mrbase_sys_staff_emergency_contact_info] }).then(async res => {
        if (res.code === '200') {
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
          await this.gettellist()
          this.btnIsLoading = false
          // this.iconstatus = !this.iconstatus
        } else {
          this.$store.commit('newsTips', this.$t('message_The_modification_failed'))
          this.btnIsLoading = false
        }
      })
    },
    telcancel () {
      this.$myConfirm({
        content: '确认取消编辑'
      }).then(res => {
        this.$router.push({
          name: 'staffmanagement'
        })
      }).catch(err => { })
    },
    datecancel () {
      this.$myConfirm({
        content: '确认取消编辑'
      }).then(res => {
        this.$router.push({
          name: 'staffmanagement'
        })
      }).catch(err => { })
    },
    godetail () {

    },
    editItem (item) {  //传了item就是修改  没传就是添加  默认传了事件对象。。
      this.$v.editedItem.$reset()
      if (item.staff_uuid || item.staff_uuid || item.bank_name || item.bank_account_number) {
        this.editedIndex = this.blanklist.indexOf(item)
        this.editedItem = Object.assign({}, item)
      }
      this.dialog = true
    },

    deleteItem (item) {
      const index = this.blanklist.indexOf(item)
      this.$myConfirm({
        content: '确认要删除吗'
      }).then(() => {
        const dd = this.blanklist.splice(index, 1)
        if (dd[0].uuid || dd[0].staff_uuid) this.deleteblanklist.push({ uuid: dd[0].uuid, "~type~": 'del' })
      })
    },
    close () {
      this.dialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    save () {  //银行弹出框保存
      this.$v.editedItem.$touch()
      if (this.$v.editedItem.$error) return
      if (this.editedIndex > -1) {
        Object.assign(this.blanklist[this.editedIndex], this.editedItem)
      } else {
        // console.log(this.editedItem);
        this.blanklist.push(this.editedItem)
      }
      this.close()
    },
    telsave () {  //紧急联系人 电话弹出框
      this.$v.editedtelItem.$touch()
      if (this.$v.editedtelItem.$error) return
      if (this.editedtelIndex > -1) {
        Object.assign(this.tellist[this.editedtelIndex], this.editedtelItem)
      } else {
        // console.log(this.editedtelItem);
        this.tellist.push(this.editedtelItem)
      }
      this.telclose()
    },
    telclose () {
      this.dialog2 = false
      this.$nextTick(() => {
        this.editedtelItem = Object.assign({}, this.defaulteltItem)
        this.editedtelIndex = -1
      })
    },
    editItemtel (item) {  //紧急联系人
      this.$v.editedtelItem.$reset()
      if (item.uuid || item.staff_uuid || item.emergency_contact_given_name) {
        this.editedtelIndex = this.tellist.indexOf(item)
        this.editedtelItem = Object.assign({}, item)
      }
      this.dialog2 = true
    },
    deleteItemtel (item) {
      const index = this.tellist.indexOf(item)
      this.$myConfirm({
        content: '确认要删除吗'
      }).then(() => {
        const dd = this.tellist.splice(index, 1)
        if (dd[0].uuid || dd[0].staff_uuid) this.deletetellist.push({ uuid: dd[0].uuid, "~type~": 'del' })
        // console.log(this.deletetellist);
      })
    }
  },
  computed: {
    headers () {
      return [
        {
          text: this.$t('label_The_name_of_the_bank'),
          align: 'start',
          sortable: false,
          value: 'bank_name',
        },
        {
          text: this.$t('label_Account_number'),
          align: 'start',
          sortable: false,
          value: 'bank_account_number',
        },
        {
          text: this.$t('label_Register_name'),
          align: 'start',
          sortable: false,
          value: 'bank_account_holder_name',
        },
        {
          text: this.$t('label_state'),
          align: 'start',
          sortable: false,
          value: 'is_used',
        },
        {
          align: 'start',
          sortable: false,
          value: 'actions',
        }
      ]
    },
    items () {
      return [
        { tab: this.$t('label_Employee_information') },
        { tab: this.$t('label_Employee_personal_information') },
        { tab: this.$t('label_Employee_employment_information') },
        { tab: this.$t('label_Employee_bank_information') },
        { tab: this.$t('label_Employee_emergency_contact') },
      ]
    },
  },
  watch: {
  },
  validations: {
    editedItem: {
      bank_name: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      bank_account_number: {
        required, numeric
      },
      bank_account_holder_name: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      effective_start_date: {
        required,
        beforeDate (value) {
          if (!this.editedItem.effective_end_date) return true
          const prevData = new Date(value).valueOf()
          const nextData = new Date(this.editedItem.effective_end_date).valueOf()
          return prevData < nextData
        }
      },
      effective_end_date: {
        required,
        afterDate (value) {
          if (!this.editedItem.effective_start_date) return true
          const nextData = new Date(value).valueOf()
          const prevData = new Date(this.editedItem.effective_start_date).valueOf()
          return prevData < nextData
        }
      },
    },
    editedtelItem: {
      emergency_contact_relationship: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      emergency_contact_given_name: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      emergency_contact_family_name: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      emergency_contact_christian_name: {
        required, nocharacter (value) {
          return this.$myVerify.nocharacter(value)
        }
      },
      emergency_contact_area_code: {
        required
      },
      emergency_contact_phone_number: {
        // required,
        // areaRequired (value) {
        //   if (value) {
        //     return this.editedtelItem.emergency_contact_area_code ? true : false
        //   } else {
        //     return true
        //   }
        // },
        // phoneVerify (value) {
        //   if (!value)
        //     return true
        //   const regex = this.$myVerify.phone
        //   return regex.test(this.editedtelItem.emergency_contact_area_code + value)
        // }
      },
      emergency_contact_email: {
        email
      },
      emergency_contact_address_line_1: {

      },
      emergency_contact_address_line_2: {

      },
      emergency_contact_address_line_3: {

      },
    },
    jobslist: {
      first_employment_date: {
        jobsbeforeDate (value) {
          if (!this.jobslist.first_employment_date && !this.jobslist.probation_end_date) return true
          const onedate = new Date(value).valueOf()
          const twodate = new Date(this.jobslist.employment_date).valueOf()
          const threedate = new Date(this.jobslist.probation_end_date).valueOf()
          return (onedate < twodate) && (onedate < threedate) && (twodate < threedate)
        }
      },
      employment_date: {
        jobsCenterDate (value) {
          if (!this.jobslist.first_employment_date && !this.jobslist.probation_end_date) return true
          const onedate = new Date(this.jobslist.first_employment_date).valueOf()
          const twodate = new Date(value).valueOf()
          const threedate = new Date(this.jobslist.probation_end_date).valueOf()
          return (onedate < twodate) && (onedate < threedate) && (twodate < threedate)
        }
      },
      probation_end_date: {
        jobsafterDate (value) {
          if (!this.jobslist.employment_date && !this.jobslist.first_employment_date) return true
          const onedate = new Date(this.jobslist.first_employment_date).valueOf()
          const twodate = new Date(this.jobslist.employment_date).valueOf()
          const threedate = new Date(value).valueOf()
          return (onedate < twodate) && (onedate < threedate) && (twodate < threedate)
        }
      },
    },
  },
};
</script>

